// 用户列表
<template>
    <ul>
        <li @click="confirmAccount(item,index)" v-for="(item, index) in userList" :key="index">
            <span>{{item}}</span>
            <img src="../../assets/publish/radio.png" v-if="ind == index">
        </li>
    </ul>
</template>

<script>
import config from '../../config.js';
import { publicPost } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
export default {
    data () {
        return {
            ind: 0,
            userList: []
        };
    },
    methods: {
        confirmAccount (item, index) {
            this.ind = index;
            // 判断跳转为发布，还是订阅页面
            if (this.$route.query.isDependable === 1 || this.$route.query.isDependable === '1') {
                this.$router.push({
                    path: '/publish',
                    query: {
                        userId: item
                    }
                });
            } else if (this.$route.query.isDependable === '2' || this.$route.query.isDependable === 2) {
                this.$router.push({
                    path: '/istrust-publish',
                    query: {
                        userId: item
                    }
                });
            } else {
                this.$router.push({
                    path: '/edit-subscibe',
                    query: {
                        userId: item
                    }
                });
            }
        }
    },
    created () {
        this.$router.setTitle(this.$t('title.editAccount'));
        // TODO: 判断是否有userId 有：代码是订阅跳转 无：代码点发布跳转
        if (this.$route.query.userId) {
            this.userList = config.userList;
            config.userList.forEach((ele, index) => {
                if (ele === this.$route.query.userId) {
                    this.ind = index;
                }
            });
        } else if (this.$route.query.isDependable === '1' || this.$route.query.isDependable === 1) {
            publicPost(getApiUrl('getTrustedUserId'), {userList: config.userList}).then((res) => {
                this.userList = res;
            }).catch(() => {
                console.warn('error');
            });
        } else if (this.$route.query.isDependable === '2' || this.$route.query.isDependable === 2) {
            this.userList = config.userList;
            config.userList.forEach((ele, index) => {
                if (ele === this.$route.query.userId) {
                    this.ind = index;
                }
            });
        }
    }
};
</script>

